"use strict";

var Constants = require('./constants');

var CropService = require('./services/crop-service');
var MediaService = require('./services/media-service');
var RestService = require('./services/rest-service');

var uuid = require('uuid-v4');

var restRoot = '/mediacat';


var associationService = new RestService({
  root: restRoot,
  resource: 'associations'
});

var categoryService = new RestService({
  root: restRoot,
  resource: 'categories'
});

var cropService = new CropService({
  root: restRoot,
  resource: 'crops'
});

var mediaService = new MediaService({
  root: restRoot,
  resource: 'images'
});


var Actions = {
  media: {
    select: function(media) {
      var mediaId = media.get('id');
      this.dispatch(Constants.MEDIA_SELECTED, {media:media});

      var query = {
        image: mediaId
      };

      var request = cropService.get(query).then(function(response) {
        var data = response.body;     
        this.dispatch(Constants.CROP_GET_SUCCESS, {data:data, request:request, mediaId:mediaId});
      }.bind(this));
      this.dispatch(Constants.CROP_GET_START, {media:media, request:request});
    },

    setRating: function(media, rating) {
      mediaService.patch(media.get('id'), {rating:rating})
      this.dispatch(Constants.MEDIA_SET_RATING, {media:media, rating:rating});
    },

    moveBefore: function(media, target) {
      this.dispatch(Constants.MEDIA_MOVE_BEFORE, {media:media, target:target});

      var patchData = this.flux.store('Media').state.get('media').map(function(media, i) {
        return {
          id: media.get('id'), 
          rank: media.get('rank')
        }; 
      });
      
      var request = mediaService.patchMany(patchData.toJS()).then(function(response) {
        var data = response.body;
        this.dispatch(Constants.MEDIA_PATCHMANY_SUCCESS, {data:data});
      }.bind(this));
      this.dispatch(Constants.MEDIA_PATCHMANY_START, {request:request, data: patchData});
    },

    moveAfter: function(media, target) {
      this.dispatch(Constants.MEDIA_MOVE_AFTER, {media:media, target:target});

      var patchData = this.flux.store('Media').state.get('media').map(function(media, i) {
        return {
          id: media.get('id'), 
          rank: media.get('rank')
        }; 
      });

      var request = mediaService.patchMany(patchData.toJS()).then(function(response) {
        var data = response.body;
        this.dispatch(Constants.MEDIA_PATCHMANY_SUCCESS, {data:data});
      }.bind(this));

      this.dispatch(Constants.MEDIA_PATCHMANY_START, {request:request, data: patchData});
    },

    addAssociation: function(category, media) {
      var categoryPath = category.get('path');
      var mediaId = media.get('id');

      var data = {
        content_type: category.get('content_type_id'),
        object_id: category.get('object_id'),
        image: mediaId
      };

      var request = associationService.create(data).then(function(response) {
        var data = response.body;
        this.dispatch(Constants.ASSOCIATIONS_CREATE_SUCCESS, {data:data, request:request, categoryPath:categoryPath, mediaId:mediaId});
      }.bind(this));
      this.dispatch(Constants.ASSOCIATIONS_CREATE_START, {category:category, media:media, request:request});
    },

    setViewMode: function(mode) {
      this.dispatch(Constants.SET_VIEW_MODE, {mode:mode});
    },

    setSort: function(sort) {
      this.dispatch(Constants.SET_MEDIA_SORT, {sort:sort});
    }
  },

  crop: {
    select: function(crop, media) {
      this.dispatch(Constants.CROP_SELECTED, {crop:crop, media:media});
    },

    deselect: function(crop, media) {
      this.dispatch(Constants.CROP_DESELECTED, {crop:crop, media:media});
    },

    move: function(crop, media, dX, dY) {
      this.dispatch(Constants.CROP_MOVE, {crop:crop, media:media, dX:dX, dY:dY});
    },

    resize: function(crop, media, dX, dY, position) {
      this.dispatch(Constants.CROP_RESIZE, {crop:crop, media:media, dX:dX, dY:dY, position:position});
    },

    add: function(media, cropType) {
      this.dispatch(Constants.CROP_ADD, {media:media, cropType:cropType});
    },

    save: function(crop) {
      var request = cropService.update(crop.get('uuid'), crop.toJS()).then(function(response) {
        var data = response.body;
        this.dispatch(Constants.CROP_SAVE_SUCCESS, {data:data, request:request, cropId: crop.get('uuid')});
      }.bind(this));
      this.dispatch(Constants.CROP_SAVE_START, {request:request, crop:crop});
    },

    pick: function(crop, previewWidth) {
      if (window.opener) {
        var request = cropService.pick(crop.get('uuid'), previewWidth).then(function(response) {
          var data = response.body;
          window.opener.dismissMediaLibrary(window, data.crop_id, data.url);
          this.dispatch(Constants.CROP_PICK_SUCCESS, {data:data, request:request, cropId: crop.get('uuid')});
        }.bind(this));
        this.dispatch(Constants.CROP_PICK_START, {request:request, crop:crop}); 
      }
    },
  },

  categories: {
    select: function(category) {
      var categoryPath = category.get('path');
      var categoryRequest = categoryService.getOne(categoryPath).then(function(response) {
        var data = response.body;
        this.dispatch(Constants.CATEGORY_GET_SUCCESS, {data:data, request: categoryRequest, categoryPath:categoryPath});
      }.bind(this));
      this.dispatch(Constants.CATEGORY_GET_START, {category:category, request: categoryRequest});

      if (category.get('accepts_images')) {
        var content_type_id = category.get('content_type_id');
        var object_id = category.get('object_id');

        var query = {
          content_type_id: content_type_id,
          object_id: object_id,
        };

        var mediaRequest = mediaService.get(query).then(function(response) {
          var data = response.body;     
          this.dispatch(Constants.MEDIA_GET_SUCCESS, {data:data, request: mediaRequest, categoryPath:categoryPath});
        }.bind(this));
        this.dispatch(Constants.MEDIA_GET_START, {category:category, request: mediaRequest});
      }
      this.dispatch(Constants.CATEGORY_SELECTED, {category:category});
    },

    open: function(category) {
      this.dispatch(Constants.CATEGORY_OPEN, {category:category});
    },

    close: function(category) {
      this.dispatch(Constants.CATEGORY_CLOSE, {category:category});
    },

    fetchChildrenSuccess: function(response) {
      var data = response.body;
      var request = response.req;
      this.dispatch(Constants.FETCH_CATEGORY_CHILDREN_SUCCESS, {data:data, request:request});
    },

    fetchChildrenError: function(response) {
      console.log(response);
    },

    loadChildren: function(category) {
      var categoryPath = category.get('path');
      var categoryRequest = categoryService.getOne(categoryPath).then(function(response) {
        var data = response.body;
        this.dispatch(Constants.CATEGORY_GET_SUCCESS, {data:data, request: categoryRequest, categoryPath:categoryPath});
      }.bind(this));
      this.dispatch(Constants.CATEGORY_GET_START, {category:category, request: categoryRequest});
    }
  },

  uploads: {
    add: function(file, category) {
      var id = uuid();
      var categoryPath = category.get('path');

      var onProgress = function(event) {
        this.dispatch(Constants.UPLOAD_PROGRESS, {event:event, id:id, file:file, categoryPath:categoryPath});
      }.bind(this);

      var onTransferComplete = function(event) {
        this.dispatch(Constants.UPLOAD_TRANSFER_COMPLETE, {event:event, id:id, file:file, categoryPath:categoryPath});
      }.bind(this);        

      var request = mediaService.upload(file, category, onProgress, onTransferComplete).then(function(response) {
        var data = response.body;
        this.dispatch(Constants.UPLOAD_SUCCESS, {data:data, id:id, file:file, categoryPath:categoryPath, request:request});
      }.bind(this));
      this.dispatch(Constants.UPLOAD_START, {id:id, file:file, categoryPath:categoryPath, request:request});
    }
  },

  dragging: {
    dragStart: function(media, x, y) {
      this.dispatch(Constants.DRAG_MEDIA_START, {media:media, x:x, y:y});
    },
    dragMove: function(x, y) {
      this.dispatch(Constants.DRAG_MEDIA_MOVE, {x:x, y:y});
    },
    dragEnd: function(x, y) {
      this.dispatch(Constants.DRAG_MEDIA_END, {x:x, y:y});
    }
  }
};

module.exports = Actions;



